<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-overlay :value="loadingComponents">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>
        <v-list-item v-if="canEditInspection">
          <v-btn class="secondary" dark @click="$router.push('/questionnaire/pp-inspection/pp-inspection/' + inspectionId +'?practitioner=' + queries.practitioner + '&application=' + queries.id)">
            <v-icon light left>mdi-pencil</v-icon>
            <span>Edit Inspection</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canChangeLocation">
          <v-btn class="secondary" dark @click="$router.push('/questionnaire/pp-change-location/pp-change-location?practitioner=' + queries.practitioner + '&application=' + queries.id)">
            <v-icon light left>mdi-swap-horizontal</v-icon>
            <span>Change Location</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="totalLicenses === 1">
          <v-btn class="secondary" dark @click="$router.push('/questionnaire/pp-generate-license/pp-generate-license/' + generateLicenseId + '?practitioner=' + queries.practitioner + '&application=' + queries.id)">
            <v-icon light left>mdi-pencil</v-icon>
            <span>Edit Profession Type</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>
        <v-list-item v-if="canAddInspection">
          <v-btn class="secondary" dark @click="$router.push('/questionnaire/pp-inspection/pp-inspection?practitioner=' + queries.practitioner + '&application=' + queries.id)">
            <v-icon light left>mdi-checkbox-multiple-marked-circle-outline</v-icon>
            <span>Add Inspection</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canApproveApplication">
          <v-btn
            :disabled="!hasInspection"
            class="primary"
            dark
            @click="activateApplicationAppDialog"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Approve/Reject</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canGenerateLicense">
          <v-btn
            @click="$router.push('/questionnaire/pp-generate-license/pp-generate-license?practitioner=' + queries.practitioner + '&application=' + queries.id)"
            class="primary"
            dark
          >
            <v-icon light left>mdi-license</v-icon>
            <span>Generate License</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canChangeLicenseStatus">
          <v-btn
            class="primary"
            dark
            @click="activateChangeStatus"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Change License Status</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canRenewLicense">
          <v-btn
            @click="renewDialog = true"
            class="primary"
            dark
          >
            <v-icon light left>mdi-autorenew</v-icon>
            <span>Renew License</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canRenewLicense">
          <v-btn
            @click="print_certificate"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Print License</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="applicationApprovalDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Process Application
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="applicationApprovalDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-progress-linear
          :active="loading"
          :indeterminate="loading"
          color="deep-purple accent-4"
          rounded
          height="6"
        ></v-progress-linear>
        <v-card-text>
          <br>
          <v-autocomplete
            :disabled="loading"
            label="Status" 
            v-model="application.applicationStatus"
            :items="appStatusCodes"
            outlined 
            hide-details="auto"
            item-text="display"
            item-value="code"
            return-object
            dense
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="processApplication" :disabled="loading">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="licenseApprovalDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Approve/Reject License
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="licenseApprovalDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-progress-linear
          :active="loading"
          :indeterminate="loading"
          color="deep-purple accent-4"
          rounded
          height="6"
        ></v-progress-linear>
        <v-card-text>
          <br>
          <v-autocomplete
            :disabled="loading"
            label="Status" 
            v-model="license.licenseStatus" 
            :items="LicenseAppStatusCodes"
            outlined 
            hide-details="auto"
            item-text="display"
            item-value="code"
            return-object
            dense
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="processLicense" :disabled="loading">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="changeLicenseStatusDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Change License Status
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="changeLicenseStatusDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-progress-linear
          :active="loading"
          :indeterminate="loading"
          color="deep-purple accent-4"
          rounded
          height="6"
        ></v-progress-linear>
        <v-card-text>
          <br>
          <v-autocomplete
            :disabled="loading"
            label="Status" 
            v-model="license.licenseStatus" 
            :items="LicenseChangeStatusCodes"
            outlined 
            hide-details="auto"
            item-text="display"
            item-value="code"
            return-object
            dense
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="processLicense" :disabled="loading">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="renewDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          License Renewal
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="renewDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-menu
            v-model="dateMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="renewalDateFormatted"
                label="Renewal Date*"
                prepend-icon="mdi-event"
                readonly
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="renewalDate"
              @change="renewalDateFormatted = formatDate(renewalDate)"
              @input="dateMenu = false"
            />
          </v-menu>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" :disabled="!renewalDate" @click="renew">Renew</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <bw-page-view :id="queries.id" page="pp-license-application" :key="appCompKey" />
    <bw-page-view :id="license.resource.id" page="pp-license" v-if="license.resource.id" :key="licenseCompKey" />
    <bw-page-view :id="generateLicenseId" page="pp-generate-license" v-if="generateLicenseId" />
    <bw-page-view :id="inspectionId" page="pp-inspection" v-if="inspectionId" />
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      loading: false,
      loadingComponents: false,
      hasInspection: true,
      inspectionId: '',
      generateLicenseId: '',
      applicationApprovalDialog: false,
      licenseApprovalDialog: false,
      changeLicenseStatusDialog: false,
      renewDialog: false,
      renewalDate: '',
      dateMenu: false,
      renewalDateFormatted: '',
      appStatusCodes: [],
      LicenseAppStatusCodes: [],
      LicenseChangeStatusCodes: [],
      appCompKey: 'application'+0,
      licenseCompKey: 'license'+0,
      application: {
        resource: {},
        statusCode: '',
        applicationStatus: {}
      },
      license: {
        resource: {},
        statusCode: '',
        licenseStatus: {}
      },
      totalLicenses: 0
    }
  },
  computed: {
    canAddInspection() {
      if(!this.hasInspection && this.$store.state.user.role === 'pp-role-data-entrant') {
        return true
      }
      return false
    },
    canEditInspection() {
      if(this.hasInspection && this.$store.state.user.role === 'pp-role-data-entrant') {
        return true
      }
      return false
    },
    canChangeLocation() {
      if(this.application.statusCode === 'approved' && this.$store.state.user.role === 'pp-role-data-entrant') {
        return true
      }
      return false
    },
    canApproveApplication() {
      if(this.application.statusCode !== 'approved' && this.$store.state.user.role === 'pp-role-manager') {
        return true
      }
      return false
    },
    canGenerateLicense() {
      if(this.application.statusCode && this.application.statusCode === 'approved' && !this.license.resource.id && this.$store.state.user.role === 'pp-role-data-entrant') {
        return true
      }
      return false
    },
    canChangeLicenseStatus() {
      if(this.license.statusCode && (this.$store.state.user.role === 'pp-role-data-entrant' || this.$store.state.user.role === 'pp-role-manager')) {
        return true
      }
      return false
    },
    canRenewLicense() {
      if(this.license.statusCode && this.license.statusCode === 'active' && (this.$store.state.user.role === 'pp-role-data-entrant' || this.$store.state.user.role === 'pp-role-manager')) {
        return true
      }
      return false
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    renew() {
      this.overlay = true;
      this.renewDialog = false
      let practitioner = this.license.resource.extension.find((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
      }).valueReference.reference
      let application = this.license.resource.extension.find((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/pp-license-application-reference'
      }).valueReference.reference
      let licensenumber = this.license.resource.extension.find((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/license-number'
      }).valueString
      let endDate
      if(this.renewalDate.split("-")[1] < 4) {
        endDate = this.renewalDate.split("-")[0] + "-03";
      } else {
        endDate = (parseInt(this.renewalDate.split("-")[0]) + 1) + "-03";
      }
      endDate = this.$moment(endDate ,"YYYY-MM").endOf("month").format("YYYY-MM-DD")
      let license = {
        resourceType: "Basic",
        meta: {
          profile: ["http://ihris.org/fhir/StructureDefinition/pp-license-profile"]
        },
        extension: [{
          url: "http://ihris.org/fhir/StructureDefinition/start-date",
          valueDate: this.renewalDate
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/end-date",
          valueDate: endDate
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/license-number",
          valueString: licensenumber
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/pp-license-status",
          valueCoding: {
            system: "http://ihris.org/fhir/CodeSystem/pp-license-status-codesystem",
            code: "active",
            display: "Active"
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/pp-license-application-reference",
          valueReference: {
            reference: application
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
          valueReference: {
            reference: practitioner
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-related-group",
          extension: [{
            url: "practitioner",
            valueString: practitioner
          }]
        }]
      }
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(license)
      }
      fetch("/fhir/Basic", opts).then((response) => {
        this.overlay = false;
        if(response.status === 201) {
          this.getLatestLicense()
          this.$store.commit("setMessage", {
            type: "success",
            text: "License Renewed",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to renew license."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to renew license."
        });
      })
    },
    getInspection() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/pp-inspection-profile&_elements=id&ppapplication=Basic/" + this.queries.id).then((response) => {
        response.json().then((inspection) => {
          if(inspection.entry && inspection.entry.length > 0) {
            this.hasInspection = true
            this.inspectionId = inspection.entry[0].resource.id
          } else {
            this.hasInspection = false
          }
          setTimeout(() => {
            this.getLatestLicense()
          }, 500);
        })
      })
    },
    getLatestLicense() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/pp-license-profile&_count=1&_sort=-_id&ppapplication=Basic/" + this.queries.id).then((response) => {
        response.json().then((license) => {
          if(license.entry && license.entry.length > 0) {
            this.license.resource = license.entry[0].resource
            this.license.licenseStatus = this.license.resource.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/pp-license-status'
            }).valueCoding
            this.license.statusCode = this.license.licenseStatus.code
            this.licenseCompKey = 'license' + ++this.licenseCompKey.split('license')[1]
          }
          setTimeout(() => {
            this.getLatestGenerateLicense()
          }, 500);
        })
      })
    },
    countLicenses() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/pp-license-profile&_count=0&ppapplication=Basic/" + this.queries.id).then((response) => {
        response.json().then((license) => {
          this.totalLicenses = license.total
        })
      })
    },
    getLatestGenerateLicense() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/pp-generate-license-profile&_count=1&_sort=-_id&_elements=id&ppapplication=Basic/" + this.queries.id).then((response) => {
        response.json().then((license) => {
          if(license.entry && license.entry.length > 0) {
            this.generateLicenseId = license.entry[0].resource.id
          }
          this.loadingComponents = false
        })
      })
    },
    getApplication() {
      fetch("/fhir/Basic/" + this.queries.id).then(response => response.json().then((application) => {
        this.application.resource = application
        this.application.applicationStatus = application.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/pp-application-status'
        }).valueCoding
        this.application.statusCode = this.application.applicationStatus.code
        this.appCompKey = 'application' + ++this.appCompKey.split('application')[1]
        setTimeout(() => {
          this.getInspection()
        }, 500);
      }))
    },
    activateApplicationAppDialog() {
      if(this.appStatusCodes.length === 0) {
        this.loadAppStatusCodes()
      }
      this.applicationApprovalDialog = true
    },
    activateLicenseAppDialog() {
      if(this.LicenseAppStatusCodes.length === 0) {
        this.loadLicenseAppStatusCodes()
      }
      this.licenseApprovalDialog = true
    },
    activateChangeStatus() {
      if(this.LicenseChangeStatusCodes.length === 0) {
        this.loadLicenseChangeStatusCodes()
      }
      this.changeLicenseStatusDialog = true
    },
    async loadAppStatusCodes() {
      this.loading = true
      this.appStatusCodes = await this.$fhirutils.expand("http://ihris.org/fhir/StructureDefinition/pp-application-status-valueset")
      this.loading = false
    },
    async loadLicenseAppStatusCodes() {
      this.loading = true
      this.LicenseAppStatusCodes = await this.$fhirutils.expand("http://ihris.org/fhir/StructureDefinition/pp-license-approval-status-valueset")
      this.loading = false
    },
    async loadLicenseChangeStatusCodes() {
      this.loading = true
      this.LicenseChangeStatusCodes = await this.$fhirutils.expand("http://ihris.org/fhir/StructureDefinition/pp-license-change-status-valueset")
      this.loading = false
    },
    processApplication() {
      let index = this.application.resource.extension.findIndex((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/pp-application-status'
      })
      this.application.resource.extension[index].valueCoding = this.application.applicationStatus
      this.applicationApprovalDialog = false
      this.overlay = true
      let opts = {
        method: "PUT",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(this.application.resource)
      }
      fetch("/fhir/Basic/" + this.application.resource.id, opts).then((response) => {
        if (response.status === 201 || response.status === 200) {
          this.getApplication()
          this.overlay = false
          this.$store.commit("setMessage", {
            type: "success",
            text: "Application Processed Successfully.",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to process application, please retry.",
          });
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to process application, please retry.",
        });
      })
    },
    processLicense() {
      let index = this.license.resource.extension.findIndex((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/pp-license-status'
      })
      this.license.resource.extension[index].valueCoding = this.license.licenseStatus
      this.licenseApprovalDialog = false
      this.changeLicenseStatusDialog = false
      this.overlay = true
      let opts = {
        method: "PUT",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(this.license.resource)
      }
      fetch("/fhir/Basic/" + this.license.resource.id, opts).then((response) => {
        if (response.status === 201 || response.status === 200) {
          this.overlay = false
          this.$store.commit("setMessage", {
            type: "success",
            text: "License Processed Successfully.",
          });
          this.getLatestLicense()
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to process license, please retry.",
          });
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to process license, please retry.",
        });
      })
    },
    print_certificate() {
      this.overlay = true
      fetch("/site/pp/print_license/" + this.queries.id + "/" + this.queries.practitioner).then((response) =>{
        response.blob().then((blob) => {
          fetch("/fhir/Practitioner/" + this.queries.practitioner).then((response) => {
            response.json().then((pract) => {
              this.overlay = false
              let fullname = ""
              let name = pract.name.find((nm) => {
                nm.use === "official"
              })
              if(!name && pract.name && pract.name.length > 0) {
                name = pract.name[0]
              }
              if(name) {
                let prefix = name.extension && name.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-prefix"
                })
                if(prefix) {
                  fullname += prefix.valueCoding.display
                }
                if(name.given) {
                  if(fullname) {
                    fullname += " "
                  }
                  fullname += name.given.join(" ")
                }
                if(name.family) {
                  if(fullname) {
                    fullname += " "
                  }
                  fullname += name.family
                }
              }
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = `${fullname}.pdf`;
              link.click();
            }).catch(() => {
              this.overlay = false
            })
          }).catch(() => {
              this.overlay = false
            })
        }).catch(() => {
          this.overlay = false
        })
      }).catch(() => {
        this.overlay = false
      })
    }
  },
  created() {
    this.loadingComponents = true
    this.getApplication()
    this.countLicenses()
  },
  components: {
    "bw-page-view": () => import("@/site/view/bw-page-view")
  }
}
</script>